import React, { useEffect } from "react";
import { useNavigate } from "react-router-dom";
import "./Home.css";
import ghost_image from "./images/ghost man 100.png";
import Map from "./Map";

function GoToRsvpForm(): void {
	const rsvp_link =
		"https://docs.google.com/forms/d/e/1FAIpQLSeIOzkkJ_-98XsXdIP78UMuh56D5LW9hBnsJpXXF9oXc7UVJA/viewform?usp=sf_link";
	window.open(rsvp_link, "_blank");
}

const Home = () => {
	const navigate = useNavigate();
	const access = localStorage.getItem("access");

	useEffect(() => {
		if (access !== "True") {
			navigate("./talk-to-me");
		}
	});

	return (
		<>
			<img
				src={ghost_image}
				className="background-image"
				alt="ghost-man"
			/>
			<div className="home-container">
				<h1 className="title text-animated">
					HOBSON
					<br />
					HALLOWEEN
					<br />
					2023
				</h1>
				<p className="text text-animated">
					Jeremy and Hannah would like to formally invite you to this
					year's Hobson Halloween party.
					<br />
					<br />
					<br />
					<span className="bold">
						Please RSVP by Friday, October 13th.
					</span>
				</p>
				<button
					className="rsvp-button text-animated"
					onClick={GoToRsvpForm}
				>
					RSVP
				</button>
				<p className="text text-animated">
					6:00 P.M.
					<br />
					Saturday, October 28th, 2023
					<br />
					<br />
					Jeremy and Hannah's House
					<br />
					16026 Osprey Ave, Chino, CA 91708
				</p>
				<br />
				<br />
				<span className="bold text text-animated">Food</span>
				<p className="text text-animated">
					<span className="creepy">
						Don't be <span className="red">chicken</span>
					</span>
					<br />
					<br />
					This year we will be serving chicken-based entres
					accompanied by some appetizers and sides. We will have a
					couple beer and soda options, but it will mostly be{" "}
					<span className="bold">BYOB</span>.
				</p>
				<br />
				<br />
				<span className="bold text text-animated">Costumes</span>
				<p className="text text-animated">
					As usual, we don't fuck around on Halloween. Costumes are
					absolutely mandatory.{" "}
					<span className="bold">No exceptions.</span>
				</p>
				<br />
				<br />
				<span className="bold text text-animated">Games</span>
				<p className="text text-animated">
					We may or may not have a game{" "}
					{/* <a href="/#/play" className="red no-underline bold">
						game
					</a>{" "} */}
					planned.
				</p>
				<br />
				<br />
				<span className="bold text text-animated">Location</span>
				<br />
				<div className="text-animated">
					<Map />
				</div>
			</div>
		</>
	);
};

export default Home;
