export const Connections = () => {
	return {
		title: "Horrorwood Networking",
		description:
			"Break down the clues piece by piece to figure out the answer to the question.",
		questions: [
			"The actor that played the character that had a crush on Diane Court was in 2 horror movies with the actor that plays the character that can't find his super suit. These two movies based on books have a very important person in common. Who is that person?",
			"For which movie did the brother of the actor that played Chris in the 1986 movie written by the first answer win the Oscar for best actor?",
			"Which psychological horror movie is based on a book written by the first answer and features an actor that played a different version of the main character from the second answer?",
			'Finish the quote from the third answer: "Here\'s _________!"',
		],
		answers: [
			["Stephen King"],
			["Joker"],
			["The Shining", "Shining"],
			["Johnny"],
		],
	};
};

export const PlexTimestamps = () => {
	return {
		title: "ComPLEX Dialogue",
		description:
			"Find the line of dialogue spoken in some of Jeremy's favorite horror movie scenes.",
		questions: [
			"IT (2017) - 28:12",
			"Cabin in the Woods (2012) - 1:15:30",
			"Annihilation (2018) - 1:32:49",
			"Split (2017) - 1:44:42",
		],
		answers: [
			["Egg Boy!", "Eggboy"],
			["Oh, shit.", "Ah shit", "shit"],
			["Annihilation."],
			["Rejoice!"],
		],
	};
};

export const SongLyrics = () => {
	return {
		title: "Ice Rhyme Kills",
		description:
			'The metal band "Ice Nine Kills" has two albums in which every song is about a different horror movie.\n\n1. Find which of their songs is about the given movie.\n2. Find the line-ending word that is sung at the given timestamp.\n3. Find the word that they use to rhyme with the word from step 2.\n\nExample: If the lyrics are "I\'m crazy in my head, everyone is dead" and the timestamp is at the word "dead," then the answer would be "head."',
		questions: [
			"Jaws - 2:06",
			"Saw - 2:20",
			"Halloween - 1:59",
			"Child's Play - 0:22",
			"The Evil Dead - 2:50",
		],
		answers: [
			["leak"],
			["machine"],
			["hour"],
			["make"],
			["goodbye", "good bye"],
		],
	};
};

export const ScavengerHunt = () => {
	return {
		title: "Scavengers",
		description:
			"Use the way that these actors' characters' died in horror movies (that were released in the given year) as clues to where you can find the answers around the party.",
		questions: [
			"Leanne Adachi - 1998",
			"Garret Dillahunt - 2009",
			"Paul Soter - 2004",
			"Mills Watson - 1983",
		],
		answers: [
			["The Throne"],
			["Heatbox"],
			["Groove Generator"],
			["Party Pooch"],
		],
	};
};

export const PhoneCall = () => {
	return {
		title: "Ring Ring",
		description: "(307) 22-GHOST",
		questions: ["Enter Password"],
		answers: [["elevator"]],
	};
};

export const TextMessage = () => {
	return {
		title: "I Spy",
		description:
			"(866) 666-4995\n\n1. Send the code word.\n2. Find the letters.\n3. Unscramble the answer.",
		questions: ["LAKE", "ROOM", "THEATER", "CEMETERY"],
		answers: [["knife"], ["scary"], ["monster"], ["fright"]],
	};
};
