import React, { useEffect, useState } from "react";
import "./Play.css";
import {
	Connections,
	PlexTimestamps,
	SongLyrics,
	ScavengerHunt,
	PhoneCall,
	TextMessage,
} from "./puzzles";

function Play() {
	const [puzzleOrder, setPuzzleOrder] = useState<
		{
			title: string;
			description: string;
			questions: string[];
			answers: string[][];
		}[]
	>([]);
	const [puzzleIndex, setPuzzleIndex] = useState<number>();
	const [questionIndex, setQuestionIndex] = useState<number>();
	const [entries, setEntries] = useState<number>();
	const [gameStartTime, setGameStartTime] = useState<number>();
	const [puzzleStartTime, setPuzzleStartTime] = useState<number>();
	const [lastCorrectTime, setlastCorrectTime] = useState<number>();
	const [guess, setGuess] = useState<string>();
	const [answerDisabled, setAnswerDisabled] = useState(false);

	useEffect(() => {
		// Get the stored puzzle order from localStorage, or generate a random order if it doesn't exist
		const storedPuzzleOrder = JSON.parse(
			localStorage.getItem("puzzleOrder")!
		);
		const storedPuzzleIndex = parseInt(
			localStorage.getItem("puzzleIndex")!
		);
		const storedQuestionIndex = parseInt(
			localStorage.getItem("questionIndex")!
		);
		const storedEntries = parseInt(localStorage.getItem("entries")!);
		const storedGameStartTime = parseFloat(
			localStorage.getItem("gameStartTime")!
		);
		const storedPuzzleStartTime = parseFloat(
			localStorage.getItem("puzzleStartTime")!
		);
		const storedlastCorrectTime = parseFloat(
			localStorage.getItem("lastCorrectTime")!
		);
		if (storedPuzzleOrder) {
			setPuzzleOrder(storedPuzzleOrder);
			setPuzzleIndex(storedPuzzleIndex);
			setQuestionIndex(storedQuestionIndex);
			setEntries(storedEntries);
			setGameStartTime(storedGameStartTime);
			setPuzzleStartTime(storedPuzzleStartTime);
			setlastCorrectTime(storedlastCorrectTime);
		} else {
			const randomOrder = shufflePuzzles();
			const currentTime = new Date().getTime();
			setPuzzleOrder(randomOrder);
			setPuzzleIndex(0);
			setQuestionIndex(0);
			setEntries(0);
			setGameStartTime(currentTime);
			setPuzzleStartTime(currentTime);
			setlastCorrectTime(currentTime);
			localStorage.setItem("puzzleOrder", JSON.stringify(randomOrder));
			localStorage.setItem("puzzleIndex", "0");
			localStorage.setItem("questionIndex", "0");
			localStorage.setItem("entries", "0");
			localStorage.setItem("gameStartTime", currentTime.toString());
			localStorage.setItem("puzzleStartTime", currentTime.toString());
			localStorage.setItem("lastCorrectTime", currentTime.toString());
		}
	}, []);

	const shufflePuzzles = () => {
		// List of all the puzzles that are imported
		const puzzles = [
			Connections(),
			PlexTimestamps(),
			SongLyrics(),
			ScavengerHunt(),
			PhoneCall(),
			TextMessage(),
		];
		for (let i = puzzles.length - 1; i > 0; i--) {
			const j = Math.floor(Math.random() * (i + 1));
			[puzzles[i], puzzles[j]] = [puzzles[j], puzzles[i]];
		}
		return puzzles;
	};

	const getEntries = () => {
		if (entries === undefined) {
			return "- entries";
		} else if (entries === 1) {
			return "1 entry";
		}
		return `${entries} entries`;
	};

	const getTitle = () => {
		if (puzzleIndex === undefined || puzzleOrder === undefined) {
			return;
		} else if (puzzleIndex >= puzzleOrder.length) {
			return <h1 className="play-title">Congrats!</h1>;
		}
		return <h1 className="play-title">{puzzleOrder[puzzleIndex].title}</h1>;
	};

	const getDescription = () => {
		if (puzzleIndex === undefined || puzzleOrder === undefined) {
			return;
		} else if (puzzleIndex >= puzzleOrder.length) {
			return (
				<h1 className="play-description">
					You finished all of the puzzles.
				</h1>
			);
		}
		return (
			<h1 className="play-description">
				{puzzleOrder[puzzleIndex].description}
			</h1>
		);
	};

	const showIncorrect = () => {
		const incorrect_element = document.querySelector(".play-incorrect")!;
		incorrect_element.classList.remove("play-hidden");
	};

	const removeIncorrect = () => {
		const incorrect_element = document.querySelector(".play-incorrect")!;
		incorrect_element.classList.add("play-hidden");
	};

	const showCorrect = () => {
		const correct_element = document.querySelector(".play-correct")!;
		correct_element.classList.remove("play-hidden");
	};

	const nextQuestion = () => {
		if (questionIndex! + 1 >= puzzleOrder[puzzleIndex!].answers.length) {
			localStorage.setItem("questionIndex", "0");
			localStorage.setItem("puzzleIndex", (puzzleIndex! + 1).toString());
			localStorage.setItem("entries", (entries! + 1).toString());
			const currentTime = new Date().getTime();
			localStorage.setItem("puzzleStartTime", currentTime.toString());
			localStorage.setItem("lastCorrectTime", currentTime.toString());
		} else {
			localStorage.setItem(
				"questionIndex",
				(questionIndex! + 1).toString()
			);
		}
	};

	const skipPuzzle = () => {
		if (
			window.confirm(
				"Are you sure you want to skip this puzzle? You will not be able to come back to it!"
			)
		) {
			if (
				window.confirm(
					"Last chance! You are forfeiting this puzzle's potential raffle entry."
				)
			) {
				localStorage.setItem("questionIndex", "0");
				localStorage.setItem(
					"puzzleIndex",
					(puzzleIndex! + 1).toString()
				);
				localStorage.setItem(
					"puzzleStartTime",
					new Date().getTime().toString()
				);
				window.location.reload();
			}
		}
	};

	const checkAnswer = (): boolean => {
		const answers = puzzleOrder[puzzleIndex!].answers[questionIndex!];
		for (let answer of answers) {
			const new_answer = answer
				.toLowerCase()
				.replace(/[^\w\s']/g, "")
				.replace(/\s+/g, " ")
				.trim();
			const new_guess = guess!
				.toLowerCase()
				.replace(/[^\w\s']/g, "")
				.replace(/\s+/g, " ")
				.trim();
			console.log(new_guess);
			if (new_guess === new_answer) {
				console.log("Correct");
				nextQuestion();
				showCorrect();
				setAnswerDisabled(true);
				setTimeout(() => {
					console.log("Reloading...");
					window.location.reload();
				}, 1000);
				return false;
			}
		}
		console.log("Incorrect");
		showIncorrect();
		return false;
	};

	const getQuestion = (index: number | undefined) => {
		if (
			puzzleIndex === undefined ||
			puzzleOrder === undefined ||
			questionIndex === undefined
		) {
			return <p className="play-text">Loading...</p>;
		} else if (puzzleIndex >= puzzleOrder.length) {
			return;
		} else if (index === undefined) {
			return (
				<p className="play-text">
					{puzzleOrder[puzzleIndex].questions[questionIndex]}
				</p>
			);
		} else {
			return (
				<p className="play-text">
					{puzzleOrder[puzzleIndex].questions[index]}
				</p>
			);
		}
	};

	const getAnswer = (index: number | undefined) => {
		if (
			puzzleIndex === undefined ||
			puzzleOrder === undefined ||
			puzzleIndex >= puzzleOrder.length
		) {
			return;
		} else if (index === undefined) {
			return (
				<input
					className="play-input"
					placeholder="Answer"
					disabled={answerDisabled}
					readOnly={answerDisabled}
					onChange={(e) => {
						setGuess(e.target.value);
						removeIncorrect();
					}}
				></input>
			);
		} else {
			return (
				<input
					className="play-input"
					disabled
					readOnly
					value={puzzleOrder[puzzleIndex].answers[index!][0]}
				></input>
			);
		}
	};

	const getPreviousQuestions = () => {
		if (questionIndex === undefined) {
			return;
		}
		let questions = [];
		for (let i = 0; i < questionIndex; i++) {
			questions.push(getQuestion(i));
			questions.push(getAnswer(i));
		}
		return questions;
	};

	const Form = () => {
		if (
			puzzleIndex === undefined ||
			puzzleOrder === undefined ||
			puzzleIndex >= puzzleOrder.length
		) {
			return;
		}
		return (
			<form
				className="play-form"
				onSubmit={(e) => {
					e.preventDefault();
					checkAnswer();
				}}
			>
				{getQuestion(undefined)}
				{getAnswer(undefined)}
				<p className="play-incorrect play-hidden">Incorrect Answer</p>
				<p className="play-correct play-hidden">Correct Answer</p>
				<button type="submit" className="play-button">
					Submit
				</button>
			</form>
		);
	};

	const Skip = () => {
		if (
			puzzleIndex === undefined ||
			puzzleOrder === undefined ||
			puzzleIndex >= puzzleOrder.length
		) {
			return;
		}
		return (
			<button
				type="button"
				className="play-skip-button"
				onClick={(e) => {
					skipPuzzle();
				}}
			>
				Skip
			</button>
		);
	};

	const [timerValue, setTimerValue] = useState(0);
	const [timerRunning, setTimerRunning] = useState(false);
	const [currentTime, setCurrentTime] = useState(new Date().getTime());

	useEffect(() => {
		let interval: string | number | NodeJS.Timeout | undefined;
		if (puzzleStartTime && timerRunning) {
			interval = setTimeout(() => {
				setCurrentTime(new Date().getTime());
				const timerValueTemp = currentTime - puzzleStartTime;
				setTimerValue(timerValueTemp >= 0 ? timerValueTemp : 0);
			}, 1000);
		}
		return () => clearInterval(interval);
	}, [currentTime, puzzleStartTime, timerRunning]);

	const getLastCorrectTime = () => {
		if (lastCorrectTime) {
			const dateTemp = new Date(lastCorrectTime);
			let hours = dateTemp.getHours();
			let ampm = hours < 12 ? "AM" : "PM";
			if (hours > 12) {
				hours -= 12;
			} else if (hours === 0) {
				hours = 12;
			}
			return (
				<>
					<span>{("0" + hours).slice(-2)}:</span>
					<span>{("0" + dateTemp.getMinutes()).slice(-2)}:</span>
					<span>{("0" + dateTemp.getSeconds()).slice(-2)}</span>
					<span> {ampm}</span>
				</>
			);
		} else {
			return undefined;
		}
	};

	const getPuzzleTimer = () => {
		const dateTemp = new Date(timerValue);
		return (
			<>
				{timerValue >= 3600000 ? (
					<span>{dateTemp.getUTCHours()}:</span>
				) : undefined}
				<span>{("0" + dateTemp.getUTCMinutes()).slice(-2)}:</span>
				<span>{("0" + dateTemp.getUTCSeconds()).slice(-2)}</span>
			</>
		);
	};

	const getGameTime = () => {
		if (lastCorrectTime && gameStartTime) {
			const gameTime = lastCorrectTime - gameStartTime;
			const dateTemp = new Date(gameTime);
			return (
				<>
					{gameTime >= 3600000 ? (
						<span>{dateTemp.getUTCHours()}:</span>
					) : undefined}
					<span>{("0" + dateTemp.getUTCMinutes()).slice(-2)}:</span>
					<span>{("0" + dateTemp.getUTCSeconds()).slice(-2)}</span>
				</>
			);
		}
		return undefined;
	};

	return (
		<>
			<div className="play-top-bar">
				<p className="play-last-correct-time">{getLastCorrectTime()}</p>
				<h2 className="play-score">{getEntries()}</h2>
				<p className="play-puzzle-timer">
					{puzzleIndex && puzzleIndex >= puzzleOrder.length
						? getGameTime()
						: getPuzzleTimer()}
				</p>
			</div>
			<hr className="play-bar-line"></hr>
			<div className="play-container">
				{getTitle()}
				{getDescription()}
				<br />
				{getPreviousQuestions()}
				{Form()}
				{Skip()}
				{timerRunning ? undefined : setTimerRunning(true)}
			</div>
		</>
	);
}

export default Play;
