import React from "react";
import "./TalkToMe.css";
import hand_image from "./images/talktome_smaller.png";

const handleChange = (event: React.ChangeEvent<HTMLInputElement>): void => {
	if (event.target.value.toLowerCase() === "i let you in") {
		console.log("Success");
		event.target.setAttribute("readonly", "readonly");
		event.target.blur();
		event.target.classList.add("fade-out-input");

		const image = document.querySelector(".hand-image")!;
		image.classList.add("animated-image");
		image.addEventListener("animationend", () => {
			image.addEventListener("animationend", () => {
				localStorage.setItem("access", "True");
				window.open("./", "_self");
			});
		});
	}
};

const TalkToMe = () => {
	return (
		<>
			<div className="container">
				<img
					src={hand_image}
					className="hand-image img-block"
					alt="talk-to-me-hand"
				/>
				<input
					autoFocus
					className="hand-input"
					onChange={handleChange}
					spellCheck="false"
				></input>
			</div>
		</>
	);
};

export default TalkToMe;
