import React from "react";
import "./Map.css";
import { GoogleMap, useLoadScript, Marker } from "@react-google-maps/api";

export default function Map() {
	const { isLoaded } = useLoadScript({
		googleMapsApiKey: "AIzaSyDWC32x2GJ70vh64v5VhrtdwlAkQjqxJJ8",
	});
	const coords = { lat: 33.962563, lng: -117.639688 };

	const style = [
		{
			elementType: "geometry",
			stylers: [
				{
					color: "#000000",
				},
			],
		},
		{
			elementType: "labels.icon",
			stylers: [
				{
					visibility: "off",
				},
			],
		},
		{
			elementType: "labels.text.fill",
			stylers: [
				{
					color: "#757575",
				},
			],
		},
		{
			elementType: "labels.text.stroke",
			stylers: [
				{
					color: "#212121",
				},
			],
		},
		{
			featureType: "administrative",
			elementType: "geometry",
			stylers: [
				{
					color: "#757575",
				},
			],
		},
		{
			featureType: "administrative.country",
			elementType: "labels.text.fill",
			stylers: [
				{
					color: "#9e9e9e",
				},
			],
		},
		{
			featureType: "administrative.locality",
			elementType: "labels.text.fill",
			stylers: [
				{
					color: "#bdbdbd",
				},
			],
		},
		{
			featureType: "poi",
			elementType: "labels.text.fill",
			stylers: [
				{
					color: "#757575",
				},
			],
		},
		{
			featureType: "poi.park",
			elementType: "geometry",
			stylers: [
				{
					color: "#181818",
				},
			],
		},
		{
			featureType: "poi.park",
			elementType: "labels.text.fill",
			stylers: [
				{
					color: "#616161",
				},
			],
		},
		{
			featureType: "poi.park",
			elementType: "labels.text.stroke",
			stylers: [
				{
					color: "#1b1b1b",
				},
			],
		},
		{
			featureType: "road",
			elementType: "geometry.fill",
			stylers: [
				{
					color: "#2c2c2c",
				},
			],
		},
		{
			featureType: "road",
			elementType: "labels.text.fill",
			stylers: [
				{
					color: "#8a8a8a",
				},
			],
		},
		{
			featureType: "road.arterial",
			elementType: "geometry",
			stylers: [
				{
					color: "#373737",
				},
			],
		},
		{
			featureType: "road.highway",
			elementType: "geometry",
			stylers: [
				{
					color: "#3c3c3c",
				},
			],
		},
		{
			featureType: "road.highway.controlled_access",
			elementType: "geometry",
			stylers: [
				{
					color: "#4e4e4e",
				},
			],
		},
		{
			featureType: "road.local",
			elementType: "labels.text.fill",
			stylers: [
				{
					color: "#616161",
				},
			],
		},
		{
			featureType: "transit",
			elementType: "labels.text.fill",
			stylers: [
				{
					color: "#757575",
				},
			],
		},
		{
			featureType: "water",
			elementType: "geometry",
			stylers: [
				{
					color: "#000000",
				},
			],
		},
		{
			featureType: "water",
			elementType: "labels.text.fill",
			stylers: [
				{
					color: "#3d3d3d",
				},
			],
		},
	];

	if (!isLoaded) return <div>Loading...</div>;
	return (
		<GoogleMap
			zoom={12}
			center={coords}
			mapContainerClassName="map-container"
			options={{
				styles: style,
				disableDefaultUI: true,
			}}
		>
			<Marker position={coords}></Marker>
		</GoogleMap>
	);
}
